<template>
  <div>
    <div
      style="width:100%;
          height:38px;
          background:rgba(13,67,111,1);
          box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
          font-size:14px;
          font-family:Microsoft YaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
          padding-left:24px;
          line-height:38px;"
      >
      服务器信息
    </div>
    <Card style="margin:10px;">
      <div style="display:flex;justify-content:flex-end;">
        <Button @click="addServerInfo" :disabled="isGray" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">录入</Button>
      </div>
      <div style="margin-top:10px;">
        <Table :loading="loading" :columns="columns1" :data="serverInfoData" :height="tabHeight"/>
        <Page 
          :current="pageNo" 
          :total="pageTotal" 
          @on-change="changePage" 
          @on-page-size-change="pageSizeChange" 
          show-sizer 
          show-total 
          show-elevator 
          placement="top" 
          style="text-align:right;margin:20px 0"
        />
      </div>
    </Card>
      <!-- 录入服务器信息Modal框 -->
    <Modal
      v-model="addServerInfoModal"
      title="服务器信息录入"
      style="color:rgba(254,254,254,1);"
      :loading="loading"
      width="600"
      >
      <Form ref="serverInfoNew" :model="serverInfoNew" :rules="serverInfoNewRule" label-position="right" :label-width="120" style="padding:0px 50px;">
        <FormItem label="服务器IP：" prop="serverHost">
          <Input type="text" v-model="serverInfoNew.serverHost" placeholder="请输入服务器IP"></Input>
        </FormItem>
        <FormItem label="服务器名称：" prop="serverName">
          <Input type="text" v-model="serverInfoNew.serverName" placeholder="请输入服务器名称"></Input>
        </FormItem>
        <FormItem label="端口：" prop="port">
          <Input type="text" v-model="serverInfoNew.port" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="用户名：" prop="userName">
          <Input type="text" v-model="serverInfoNew.userName" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="密码：" prop="password">
          <Input type="password" v-model="serverInfoNew.password" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="备注：" prop="remarks">
          <Input type="textarea" v-model="serverInfoNew.remarks" placeholder="请输入"></Input>
        </FormItem>
      </Form>
      <p slot="footer" style="margin-right:200px;">
        <Button @click="cancelAddServerInfo" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">取消</Button>
        <Button type="primary" @click="confirmAddServerInfo" style="margin-left:40px;">确定</Button>
      </p>
    </Modal>
    <!-- 修改服务器信息Modal框 -->
    <Modal
      v-model="updateServerInfoModal"
      :loading="loading"
      title="修改服务器信息"
      width="600"
      >
      <Form ref="serverInfoUpdate" :model="serverInfoUpdate" :rules="serverInfoUpdateRule" :label-width="100" style="padding:0px 50px;">
        <FormItem label="服务器IP：" prop="serverHost">
          <Input type="text" v-model="serverInfoUpdate.serverHost" placeholder="请输入服务器IP"></Input>
        </FormItem>
        <FormItem label="服务器名称：" prop="serverName">
          <Input type="text" v-model="serverInfoUpdate.serverName" placeholder="请输入服务器名称"></Input>
        </FormItem>
        <FormItem label="端口：" prop="port">
          <Input type="text" v-model="serverInfoUpdate.port" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="用户名：" prop="userName">
          <Input type="text" v-model="serverInfoUpdate.userName" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="密码：" prop="password">
          <Input type="password" v-model="serverInfoUpdate.password" placeholder="请输入"></Input>
        </FormItem>
        <FormItem label="备注：" prop="remarks">
          <Input type="textarea" v-model="serverInfoUpdate.remarks" placeholder="请输入"></Input>
        </FormItem>
      </Form>
        <p slot="footer" style="margin-right:200px;">
          <Button @click="cancelUpdateServerInfo" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">取消</Button>
          <Button type="primary" @click="confirmUpdateServerInfo()" style="margin-left:40px;">确定</Button>
        </p>
    </Modal>

  </div>
</template>

<script>
import api from '@/api/api';

export default {
  data () {
    return {
      serverInfoNew:{ // 服务器信息录入 表单校验
        serverHost:'',
        serverName:'',
        port:'',
        userName:'',
        password:'',
        remarks:'',
      },
      serverInfoNewRule:{
        serverHost:[
          { required: true, message: '服务器IP不能为空', trigger: 'blur' },
          { pattern: /^((25[0-5]|2[0-4]\d|[01]?\d\d?)($|(?!\.$)\.)){4}$/u, message: '服务器IP格式不正确，请重新输入！', trigger: 'blur' },
        ],
        serverName:[
          {
            required: true,
            message: '服务器名称不能为空',
            trigger: 'blur'
          }
        ],
        port:[
          { 
            required: true, 
            message: '端口号不能为空', 
            trigger: 'blur' 
          },
          { 
            pattern: /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, 
            message: '端口号格式不正确，请输入0~65535之间的数字', 
            trigger:'blur'
          },
        ],
        userName:[
          {
            required: true,
            message: '用户名不能为空',
            trigger: 'blur'
          }
        ],
        password:[
          {
            required: true,
            message: '密码不能为空',
            trigger: 'blur'
          }
        ],
        remarks:[
          {
            required: true,
            message: '备注信息不能为空',
            trigger: 'blur'
          }
        ]
      },

      serverInfoUpdate:{// 服务器信息修改 表单校验
        serverHost:'',
        serverName:'',
        port:'',
        userName:'',
        password:'',
        remarks:'',
      },
      serverInfoUpdateRule:{
        serverHost:[
          {
            required: true,
            message: '服务器IP不能为空',
            trigger: 'blur'
          },
          { 
            pattern: /^((25[0-5]|2[0-4]\d|[01]?\d\d?)($|(?!\.$)\.)){4}$/u, 
            message: '服务器IP格式不正确，请重新输入！', 
            trigger: 'blur' 
          },
        ],
        serverName:[
          {
            required: true,
            message: '服务器名称不能为空',
            trigger: 'blur'
          }
        ],
        port:[
          {
            required: true,
            message: '端口号不能为空',
            trigger: 'blur'
          },
          { 
            pattern: /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/, 
            message: '端口号格式不正确，请输入0~65535之间的数字', 
            trigger:'blur'
          },
        ],
        userName:[
          {
            required: true,
            message: '用户名不能为空',
            trigger: 'blur'
          }
        ],
        password:[
          {
            required: true,
            message: '密码不能为空',
            trigger: 'blur'
          }
        ],
        remarks:[
          {
            required: true,
            message: '备注信息不能为空',
            trigger: 'blur'
          }
        ]
      },
      serverInfoData:[], // 服务器信息列表
      addServerInfoModal:false, // 录入服务器信息 弹窗
      updateServerInfoModal:false, // 修改服务器信息 弹窗
      // 分页
      current:1,
      pageNo:1,
      pageSize:10,
      pageTotal:10,
      loading:false,
      serverInfoId:'',// 服务器信息 主键id

      columns1: [
        {
          title: '服务器IP',
          key: 'serverHost',
          // minWidth:140,
        },
        {
          title: '服务器名称',
          key: 'serverName'
        },
        {
          title: '备注',
          key: 'remarks'
        },
        {
          title: '当前存储空间',
          key: 'current',
          minWidth:170,
        },
        {
          title: '操作',
          key: 'handle',
          align: 'center'
        }
      ],
      isGray:false, // 根据操作员登录时拿到的operationPermissions的值（默认为false），来判断该操作员是否拥有相应的权限，若为false，则相应的功能置灰。\
      
      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
    }
  },
  created(){
    this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true;
    this.queryServerInfoList(),
    this.init()
  },
  mounted(){
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    init(){
      let vm = this
      this.columns1.forEach(item => {
        if (item.key === 'handle') {
          item.render = (h, param) => {
            return h('p', [
              h(
                'span',
                  {
                    props:{
                      type:'text',
                      // disabled:this.isGray
                    },
                    style:{
                      marginRight:'10px',
                      color:this.isGray?'gray':'rgba(2,232,255,1)',
                      cursor:this.isGray?'not-allowed':'pointer',
                    },
                    on: {
                      click: () => {
                        if(vm.isGray){
                          return false;
                        }
                        vm.updateServerInfo(param.row.id);
                        vm.queryServerInfoById();
                      }
                    }
                  },
                  "修改"
              ),
              h(
                'Poptip',
                {
                  props: {
                    confirm:true,
                    title:"您确定要删除这条数据吗？",
                    transfer:true
                  },
                  on:{
                    'on-ok':() => {
                      vm.deleteServerInfoById(param.row.id)
                    }
                  }
                },
                [
                  h(
                    'span',
                    {
                      // props:{
                      //   type:'text',
                      //   size: 'small',
                      //   disabled:this.isGray
                      // },
                      style:{
                        marginRight:'0',
                        color:'rgba(2,232,255,1)',
                        // cursor:'pointer',
                        cursor:'pointer',
                        display:this.isGray?'none':''
                        // backgroundColor:'rgba(13,67,111,1)',
                      }
                    },
                    "删除"
                  )
                ]
              ),
              h('span',{
                style:{
                  marginRight:'0',
                  color:'gray',
                  cursor:'not-allowed',
                  display:this.isGray?'':'none'
                }
              }, '删除')
            ]);
          }
        }
      })
    },

    queryServerInfoList(){ // 查询服务器信息列表
      let param = {
        pageNo:this.pageNo,
        pageSize:this.pageSize,
      }

      this.loading = true;
      api.queryServerInfoJson(param).then(
        res => {
          
          // console.log(res)
          this.serverInfoData = res.result;

          if (res.result.length === 0 && this.pageNo > 1) {
            this.pageNo --;
            this.queryServerInfoList()
          }

          this.pageNo = res.pageNo;
          this.pageSize = res.pageSize;
          this.pageTotal = res.count;
          this.loading = false;
        }
      ),
      err => {
        this.loading = false;
      }
    },

    // 录入 服务器信息
    addServerInfo(){
      this.$refs['serverInfoNew'].resetFields(); // 打开 新建弹窗时清空数据
      this.addServerInfoModal = true;
    },
    // 取消 录入 服务器信息
    cancelAddServerInfo(){
      this.addServerInfoModal = false
    },
    // 确定 录入 服务器信息
    confirmAddServerInfo(){
      this.$refs.serverInfoNew.validate((valid) => {
        if (valid) {
          let param = {
            serverHost: this.serverInfoNew.serverHost,
            serverName: this.serverInfoNew.serverName,
            userName: this.serverInfoNew.userName,
            password: this.serverInfoNew.password,
            port: this.serverInfoNew.port,
            remarks: this.serverInfoNew.remarks,
          }
          this.loading = true;

          api.createServerInfoJson(param).then(
            res => {
              this.$Message.success('服务器信息录入成功，后台正在校验信息，请稍候~');
              this.$refs['serverInfoNew'].resetFields();
              this.loading = false;
              this.addServerInfoModal = false;
              this.queryServerInfoList();
            },
            err => {
              this.loading = false
              // this.addServerInfoModal = false
              this.queryServerInfoList()
            }
          )
        } 
        // else {
        //     this.$Message.error('录入失败!');
        // }
      })
    },
    updateServerInfo(params){
      this.updateServerInfoModal = true;
      this.serverInfoId = params;
      this.$refs['serverInfoUpdate'].resetFields();
    },

    queryServerInfoById(){ 
      let param = {
        id: this.serverInfoId,
      }
      // this.loading = true;

      api.queryServerInfoJsonById(param).then( // 根据主键id查询 服务器信息详情  数据回显
        res => {
          // console.log(res.result);
          this.loading = false,
          this.serverInfoUpdate.serverHost = res.result.serverHost,
          this.serverInfoUpdate.serverName = res.result.serverName,
          this.serverInfoUpdate.userName = res.result.userName,
          this.serverInfoUpdate.password = res.result.password,
          this.serverInfoUpdate.port = (res.result.port).toString(),// 后台该字段为Number类型，若涉及修改，从后台取数据的时候需要toString(),传数据给后台时需要parseInt("字符串变量")
          console.log(this.serverInfoUpdate.port)
          this.serverInfoUpdate.remarks = res.result.remarks
        },
        err => {
          this.loading = false;
          this.$Message.error('系统异常!');
        }
      )
    },

    // 取消 修改服务器信息
    cancelUpdateServerInfo(){
      this.updateServerInfoModal = false
    },
    // 确定 修改服务器信息
    confirmUpdateServerInfo(){
      this.$refs.serverInfoUpdate.validate((valid) => {
        if (valid) {
          let param = {
            id: this.serverInfoId,
            serverHost: this.serverInfoUpdate.serverHost,
            serverName: this.serverInfoUpdate.serverName,
            userName: this.serverInfoUpdate.userName,
            password: this.serverInfoUpdate.password,
            port: parseInt(this.serverInfoUpdate.port),  // 后台该字段为Number类型，若涉及修改，从后台取数据的时候需要toString(),传数据给后台时需要parseInt("字符串变量")
            remarks: this.serverInfoUpdate.remarks,
          }
          
          this.loading = true;

          api.updateServerInfoJson(param).then(
            res => {
              this.$Message.success('服务器信息修改成功，后台正在校验信息，请稍候~');
              this.$refs['serverInfoUpdate'].resetFields();
              this.loading = false;
              this.updateServerInfoModal = false;
              this.queryServerInfoList();
            },
            err => {
              this.loading = false;
              this.queryServerInfoList();
            }
          )
        } 
        // else {
        //     this.$Message.error('修改失败!');
        // }
      })
    },
    deleteServerInfoById(id){// 删除气泡 确认
      let param = {
        id: id,
      }

      api.deleteServerInfoJson(param).then(
        res => {
          this.$Message.success('服务器信息删除成功!');
          this.queryServerInfoList();
        }
      )
    },

    /** 改变页面 */ 
    changePage(Num) {
      this.pageNo = Num;
      this.queryServerInfoList();
    },

    /** 改变页面条数 */
    pageSizeChange(size){
      this.pageSize = size
      if(this.pageNo === 1){
        this.queryServerInfoList();
      }
    }
  }
}
</script>